@charset "UTF-8";
body {
  min-width: 320px;
  max-width: 1700px;
  margin: auto;
  font-family: "TT Lakes", "Arial", sans-serif;
  font-size: 16px;
}

.substrate {
  width: 100vw;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.9);
  position: fixed;
  z-index: 10;
  display: none;
}

.substrate.show-substrate {
  display: block;
}

@media screen and (min-width: 1440px) {
  .main-container {
    display: flex;
    justify-content: center;
  }
}
.burger-menu {
  display: none;
  position: fixed;
  z-index: 11;
  top: 0;
  left: 0;
  height: 100vh;
  background-color: #ffffff;
}
.burger-menu__wrapper {
  width: 320px;
  height: 100vh;
  box-shadow: 16px 0 52px rgba(14, 24, 80, 0.2);
  padding-left: 24px;
  padding-right: 24px;
}
.burger-menu__header {
  display: flex;
  flex-wrap: nowrap;
  width: 100%;
  height: 88px;
  justify-content: space-between;
  align-items: center;
}
.burger-menu__header-left {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 12px;
}
.burger-menu__header-right {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.burger-menu__menu {
  padding-left: 0;
}

.button {
  border-style: none;
  width: 40px;
  height: 40px;
}

.button:hover {
  opacity: 0.5;
  transition-duration: 0.7s;
}

.burger-close__button {
  display: none;
  background-image: url(./static/ic_burger_close.svg);
  margin-right: 17px;
}

.burger-menu__logo {
  align-items: center;
}

.search__button {
  background-image: url(./static/ic_search.svg);
}

.menu__button {
  position: relative;
  margin-bottom: 32px;
  list-style: none;
}

.menu__link {
  font-family: "TT Lakes Regular";
  text-decoration: none;
  color: #1b1c21;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.2px;
  text-align: left;
}

.burger-menu__call__button {
  background-image: url(./static/ic_call.svg);
  margin-right: 0.7vw;
  cursor: pointer;
}

.burger-menu__chat__button {
  background-image: url(./static/ic_chat.svg);
  margin-right: 0.7vw;
}

.burger-menu__profile__button {
  background-image: url(./static/ic_profile.svg);
}

.menu__link:hover {
  color: #7e7e82;
  transition-duration: 0.7s;
}

.menu__button:hover::before {
  position: absolute;
  display: block;
  width: 4px;
  height: 32px;
  left: -24px;
  top: -5px;
  background-color: #41f6d7;
  content: "";
  border-bottom-right-radius: 20px;
  border-top-right-radius: 20px;
}

.wrapper-services-footer {
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.burger-menu__services {
  margin-bottom: auto;
}

.burger-menu__footer__buttons {
  margin-top: auto;
  display: flex;
  justify-content: flex-start;
  margin-bottom: 1vw;
}

.mail {
  text-decoration: none;
  display: inline-block;
  color: #1b1c21;
  font-family: "TT Lakes Regular";
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.2px;
  text-align: left;
}

.phone {
  display: block;
  text-decoration: none;
  padding-top: 12px;
  color: #1b1c21;
  font-family: "TT Lakes Bold";
  font-size: 24px;
  font-weight: 700;
  line-height: 32px;
  letter-spacing: -0.6px;
  text-align: left;
}

.language {
  padding-top: 40px;
  display: flex;
  justify-content: flex-start;
  margin-left: 0;
}

.language__button {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background-color: transparent;
  border: none;
  font-family: "TT Lakes Regular";
  font-weight: inherit;
  line-height: inherit;
  color: inherit;
  cursor: pointer;
}

.language__link:hover {
  color: #7e7e82;
  transition-duration: 0.7s;
}

@media screen and (min-width: 1440px) {
  .burger-menu {
    display: block;
    position: static;
    z-index: 9;
  }
  .burger-close__button {
    display: none !important;
  }
  .burger-menu__wrapper {
    box-shadow: none;
  }
}
.burger-menu.show--burger {
  display: block;
}

@media screen and (min-width: 768px) {
  .burger-close__button {
    display: block;
  }
}
@media screen and (min-width: 320px) {
  .burger-close__button {
    display: block;
  }
}
.repairBrand {
  margin-bottom: 2vw;
}

.repairBrand__title {
  font-family: "TT Lakes Medium";
  color: #7e7e82;
  font-size: 16px;
  letter-spacing: -0.6px;
  margin: 0;
  border-top: 3px solid #d9fff5;
  padding: 1vw 2vw;
  background-image: linear-gradient(to right, #f8f8f8, #ffffff);
}

.brand-slider {
  padding: 0 2vw;
  overflow: hidden;
  margin-bottom: 1vh;
  height: 185px;
}

.brand-list {
  padding: 0;
  margin: 1vh 0 1vh 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}

.brand-list-item {
  background-color: #ffffff;
  padding: 1px;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  border: 1px solid #eaeaea;
  border-radius: 6px;
  height: 72px;
  align-items: center;
}

.go-button {
  border-style: none;
  width: 40px;
  height: 40px;
  background-color: inherit;
  background-image: url(./static/go.svg);
  text-align: center;
  margin: 10px;
}

.image {
  margin: 16px;
  background-color: inherit;
}

.expand_button {
  background-color: inherit;
  margin-left: 40px;
  justify-items: center;
  border-style: none;
  font-family: "TT Lakes Regular";
  font-weight: 500;
  font-size: 14px;
  cursor: -webkit-grab;
  cursor: grab;
  position: relative;
}

.expand_button::before {
  content: url(./static/ic_expand.svg);
  top: -2px;
  left: -23px;
  position: absolute;
  transition: transform 0.3s ease;
}

.expand_button.rotate::before {
  transform: translateY(-5px) rotate(180deg);
}

@media (max-width: 767px) {
  .brand-slider {
    overflow-x: hidden;
    height: auto;
  }
  .brand-list {
    margin-bottom: 20px;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .brand-list-item {
    background-color: #ffffff;
    margin: 1px;
    margin-top: 5px;
    margin-bottom: 5px;
    padding: 1px;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    border: 1px solid #eaeaea;
    border-radius: 6px;
    max-width: 100%;
    height: 72px;
    align-items: center;
    flex-shrink: 0.002; /*последний элемент не влазил*/
  }
  .expand__brands {
    display: none !important;
  }
}
@media screen and (min-width: 768px) {
  .brand-list {
    display: grid;
    grid-template-columns: repeat(3, minmax(240px, 1fr));
    gap: 16px 24px;
  }
}
@media screen and (min-width: 1440px) {
  .brand-list {
    display: grid;
    grid-template-columns: repeat(4, minmax(240px, 1fr));
    gap: 16px 24px;
  }
}
.brand-slider.show {
  height: auto;
  overflow: visible;
  margin-bottom: 46px;
}

.main-content {
  background-color: #f8f8f8;
}

.header {
  display: flex;
  width: 100%;
  height: 88px;
  justify-content: space-between;
  align-items: center;
  background-color: #ffffff;
  background-size: cover;
}

.left-header {
  display: flex;
  background-color: #ffffff;
  align-items: center;
}

.right-header {
  display: flex;
  background-color: #ffffff;
}

.header::before {
  content: "";
  background: #b5b6bc;
  left: 59vw;
  top: 28px;
  bottom: 28px;
  width: 2px;
  height: 32px;
  display: block;
  position: absolute;
  border-radius: 15%;
}

.header::after {
  content: "";
  background: #d9fff5;
  left: 0;
  right: 0;
  top: 87px;
  bottom: 0px;
  width: 100%;
  height: 1px;
  display: block;
  position: absolute;
}

.button {
  border-style: none;
  width: 40px;
  height: 40px;
  background-color: #ffffff;
}

.burger,
.logo,
.call,
.chat,
.profile,
.repair,
.checkstatus {
  background-color: #ffffff;
}

.burger__button {
  background-image: url(./static/ic_burger.svg);
  margin-left: 3vw;
}

.logo {
  margin-left: 3vw;
}

.call__button {
  background-image: url(./static/ic_call.svg);
  display: none;
  margin-right: 2vw;
}

.chat__button {
  background-image: url(./static/ic_chat.svg);
  display: none;
  margin-right: 2vw;
}

.profile__button {
  background-image: url(./static/ic_profile.svg);
  display: none;
  margin-right: 5vw;
}

.repair__button {
  background-image: url(./static/ic_repair.svg);
  margin-right: 2vw;
  position: relative;
}

.checkstatus__button {
  background-image: url(./static/ic_checkstatus.svg);
  margin-right: 3vw;
}

.main-header {
  display: flex;
  align-items: center;
  padding: 0 2vw 0 2vw;
}

.main-header__h1 {
  color: #1b1c21;
  font-family: "TT Lakes Bold";
  font-size: 2em;
  font-weight: bold;
  font-style: normal;
  letter-spacing: -0.6px;
  width: 100%;
}

.main-header__h1::before {
  content: url(./static/highlight.svg);
  position: absolute;
  left: 0;
}

label {
  display: none;
}

.nav {
  position: relative;
  padding: 0 2vw 0 2vw;
  overflow: hidden;
}

.scroll-wrapper {
  padding-bottom: 200px;
  margin-bottom: -200px;
  overflow: auto;
}

.slide-menu {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  white-space: nowrap;
  min-height: 40px;
  position: relative;
  padding-left: 0;
}

.slide-menu__button {
  list-style: none;
  background-color: #f8f8f8;
  padding: 0.7rem;
}

.slide-menu__button > a {
  padding: 5px;
  color: #7e7e82;
  font-family: "TT Lakes Medium";
  font-weight: 500;
  font-size: 1rem;
  text-decoration: none;
  letter-spacing: -0.2px;
}

.slide-menu__button:hover {
  border-style: solid;
  border-color: #41f6d7;
  background-color: #ffffff;
  border-width: 2px;
  border-radius: 5px;
  transition-duration: 0.2;
}

.slide-menu__button--active > a {
  background-color: #ffffff;
}

.content {
  display: flex;
  flex-direction: column;
  padding: 0 2vw 3vw 2vw;
}

.content-text {
  color: #1b1c21;
  font-family: "TT Lakes Rgular";
  font-size: 1rem;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: 0.2px;
  padding-bottom: 10px;
}

.content-text__text {
  font-family: "TT Lakes Regular";
  max-width: 100%;
  padding-right: 1vw;
  font-size: 0.88rem;
}

.content-text__text--pad {
  display: none;
}

.content__text--computer {
  display: none;
}

.content-image__image {
  width: 100%;
}

.main-header__label {
  min-width: 140px;
  font-family: "TT Lakes Medium";
  color: #1b1c21;
  font-weight: 500;
  font-size: 1rem;
  cursor: default;
  align-self: flex-end;
  position: relative;
  padding-bottom: 12px;
}

.main-header__repair-button {
  background-image: url(./static/ic_repair.svg);
  background-repeat: no-repeat;
  margin-right: 2vw;
  align-self: flex-end;
  display: none;
}

.main-header__checkstatus-button {
  background-image: url(./static/ic_checkstatus.svg);
  background-repeat: no-repeat;
  align-self: flex-end;
  display: none;
}

@media (min-width: 768px) {
  .header::before {
    left: calc(40px + 6vw);
  }
  .repair__button::before {
    content: "";
    background: #eaeaea;
    right: 6.5vw;
    top: 5px;
    width: 2px;
    height: 32px;
    position: absolute;
    border-radius: 15%;
  }
  .call__button,
  .chat__button,
  .profile__button {
    display: block;
  }
  label {
    display: none;
  }
  .main-header__repair-button,
  .main-header__checkstatus-button {
    display: none;
  }
  .nav {
    padding: 0 2vw 0 2vw;
  }
  .scroll-wrapper {
    overflow-x: auto;
  }
  .slide-menu {
    flex-wrap: nowrap;
  }
  .content {
    flex-direction: row;
  }
  .content-text__text--pad {
    display: inline;
  }
  .content-image__image {
    object-fit: cover;
    width: 360px;
    height: 208px;
    border-radius: 3%;
  }
  .content__text--computer {
    display: none;
  }
}
@media (min-width: 1440px) {
  .main {
    margin-left: auto;
    margin-right: auto;
  }
  .header {
    display: none;
  }
  .content-image__image {
    object-fit: cover;
    width: 532px;
    height: 307px;
    border-radius: 3%;
  }
  .nav {
    padding: 0 2vw 0 2vw;
  }
  .slide-menu {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .content__text--computer {
    display: inline;
  }
  .expand__button {
    padding-left: 7vw;
  }
  label,
  .main-header__repair-button,
  .main-header__checkstatus-button {
    display: block;
  }
}
.content-text__text--pad.show_text {
  display: inline;
}

.content__text--computer.show_text {
  display: inline;
}

