  }
  .price__swiper {
    background-color: transparent;
    margin: 0;
  }
  .card__item--services {
    line-height: 12px;
  }
}
.popup {
  display: none;
  position: fixed;
  z-index: 11;
  top: 0;
  right: 0;
}
.popup__container {
  min-width: 320px;
  height: 100vh;
  padding: 24px 16px;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  box-shadow: -16px 0 52px rgba(14, 24, 80, 0.2);
}
.popup__icon {
  margin-bottom: 32px;
}
.popup__title {
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.6px;
  font-weight: bold;
  display: flex;
  align-items: center;
  position: relative;
  margin-bottom: 24px;
}
.popup__title::before {
  width: 4px;
  height: 40px;
  background-color: #41f6d7;
  left: -16px;
  border-radius: 0 4px 4px 0;
  position: absolute;
  content: "";
}

.form__input {
  border: 1px solid #b5b6bc;
  border-radius: 6px;
  line-height: 24px;
  letter-spacing: -0.2px;
  padding: 12px 16px;
  width: 93%;
  margin-bottom: 16px;
  color: #1b1c21;
}
.form__input::-webkit-input-placeholder {
  color: #b5b6bc;
}
.form__input::-moz-placeholder {
  color: #b5b6bc;
}
.form__input:-ms-input-placeholder {
  color: #b5b6bc;
}
.form__input::placeholder {
  color: #b5b6bc;
}
.form__text {
  margin-bottom: 18px;
}
.form__btn {
  border-width: 0;
  border-radius: 16px;
  background-color: #ff3e79;
  color: #ffffff;
  line-height: 24px;
  font-size: 12px;
  letter-spacing: -0.15px;
  font-weight: 500;
  text-transform: uppercase;
  padding: 4px 8px 4px 12px;
  display: flex;
  margin-left: auto;
}

.text {
  font-size: 14px;
  line-height: 16px;
  color: #7e7e82 .text --pink;
  color-color: #ff3e79;
}

.popup__icon {
  background-image: url(./static/ic_burger_close.svg);
  width: 40px;
  height: 40px;
  transition: transform 0.2s;
  border: none;
  background-color: transparent;
}
.popup__icon:hover {
  transform: scale(1.1);
}

.visually-hidden {
  line-height: 0;
  font-size: 0;
  color: transparent;
}

@media screen and (min-width: 768px) {
  .popup__container {
    width: 320px;
    padding: 24px;
  }
  .popup__title::before {
    left: -24px;
  }
}
@media screen and (min-width: 1440px) {
  .popup__container {
    width: 440px;
    padding: 32px;
  }
  .popup__icon {
    position: absolute;
    left: -72px;
    top: 32px;
  }
  .popup__title {
    margin-top: 4px;
  }
  .popup__title::before {
    left: -34px;
    border-radius: 4px;
  }
}
.popup--call.show__popup--call {
  display: block;
}

.popup--feedback.show__popup--feedback {
  display: block;
}

.swiper-slide {
  box-sizing: border-box;
}

@media screen and (max-width: 768px) {
  .swiper {
    margin-top: 16px;
    padding: 0 16px 44px;
  }
  .swiper-pagination {
    position: absolute;
  }
  .swiper-pagination-bullet {
    height: 12px;
    width: 12px;
    background-color: #dddddd;
  }
  .swiper-pagination-bullet-active {
    background-color: #b5b6bc;
  }
}